import { timer } from './components/startTimer';
function countQuestionTypeFor(type) {
    const ctx = window.testContext;
    const { questions, generatedCorpus } = ctx;

    const selectedQuestions = questions.filter(q =>
        generatedCorpus.includes(q.id)
    );

    return selectedQuestions.filter(q => q.categorie === type).length;
}

function computeResult() {
    const ctx = window.testContext;
    const { questions, generatedCorpus, userAnswers } = ctx;

    const rightAnswersCount = {
        comprehension: 0,
        knowledge: 0,
        behavior: 0
    };

    for (let cnt = 0; cnt < userAnswers.length; cnt++) {
        let userAnswer = userAnswers[cnt];
        const question = questions.find(q => q.id === generatedCorpus[cnt]);

        if (question.gameplay === 'txt_tr') {
            userAnswer = userAnswer.map((a, index) =>
                (a === question.reponses[index].correct).toString()
            );
        }

        const extractedValues = userAnswer.map(
            a => (typeof a !== 'string' ? a.value : a)
        );
        const isCorrect = !extractedValues.some(a => a === 'false');

        if (isCorrect) {
            switch (question.categorie) {
                case 'compréhension':
                    rightAnswersCount.comprehension += 1;
                    break;
                case 'connaissance':
                    rightAnswersCount.knowledge += 1;
                    break;
                case 'comportement':
                    rightAnswersCount.behavior += 1;
                    break;
            }
        }
    }

    const { comprehension, knowledge, behavior } = rightAnswersCount;

    return {
        overallSuccessRate:
            (comprehension + knowledge + behavior) / generatedCorpus.length,
        comprehensionSuccessRate:
            comprehension / countQuestionTypeFor('compréhension'),
        knowledgeSuccessRate: knowledge / countQuestionTypeFor('connaissance'),
        behaviorSuccessRate: behavior / countQuestionTypeFor('comportement')
    };
}

function loadResult(pageResult) {
    const {
        overallSuccessRate,
        comprehensionSuccessRate,
        knowledgeSuccessRate,
        behaviorSuccessRate
    } = computeResult();

    var header,
        content,
        title,
        lead,
        badgeBox,
        badge,
        diagContainer,
        diagBox,
        diag,
        btnSite,
        anchor,
        canvas;
    //clear timer
    clearInterval(timer);
    //clean body...
    removeAllChild(pageResult);

    //Creat Content RESULT => BADGE & DIAGRADAR
    //diag radar
    // diag = document.createElement('img');
    // diag.className = 'radarImg';
    // diag.src = '../assets/img/diagramme.png';
    // diagBox = document.createElement('div');
    // diagBox.className = 'diag_box';
    // diagBox.appendChild(diag);

    canvas = document.createElement('canvas');

    diagBox = document.createElement('div');
    diagBox.className = 'diag';
    diagBox.appendChild(canvas);

    diagContainer = document.createElement('div');
    diagContainer.className = 'diag_container';
    diagContainer.appendChild(diagBox);

    const ctx = canvas.getContext('2d');
    const chart = new Chart(ctx, {
        type: 'radar',
        label: '',
        data: {
            labels: ['Connaissances', 'Compréhension', 'Comportement'],
            datasets: [
                {
                    data: [
                        knowledgeSuccessRate * 100,
                        comprehensionSuccessRate * 100,
                        behaviorSuccessRate * 100
                    ]
                }
            ]
        },
        options: { scale: { ticks: { min: 0, max: 100 } } }
    });

    //badge
    badge = document.createElement('p');
    badge.className = 'badge';
    //temp => add result pt
    badge.innerHTML = overallSuccessRate * 100 + '% de bonnes réponses';
    //badgeBox
    badgeBox = document.createElement('div');
    badgeBox.className = 'badge_box';
    badgeBox.appendChild(badge);

    //add CONTENT
    content = document.createElement('div');
    content.className = 'content_result';
    content.appendChild(badgeBox);
    content.appendChild(diagContainer);

    //create HEADER content TITLE & LEAD
    //creat title
    title = document.createElement('h1');
    title.className = 'title_result';
    title.innerHTML = 'Résultats';
    //creat lead
    lead = document.createElement('p');
    lead.className = 'lead_result';
    lead.innerHTML =
        'Bravo, vous avez réussi à répondre à toutes les questions dans les temps !';
    //creat header & add contents
    header = document.createElement('header');
    header.className = 'head_result';
    header.appendChild(title);
    header.appendChild(lead);

    // add BTN return site
    anchor = document.createElement('a');
    anchor.href = 'http://easy-driver-test.webflow.io/';
    anchor.innerHTML = 'retour au site';
    btnSite = document.createElement('button');
    btnSite.className = 'btn_site';
    btnSite.type = 'button';
    btnSite.appendChild(anchor);
    btnSite.addEventListener('click', function() {
        location.replace('http://easy-driver-test.webflow.io/');
    });

    //ADD ALL in PAGERESULT
    pageResult.appendChild(header);
    pageResult.appendChild(content);
    pageResult.appendChild(btnSite);
    pageResult.className = 'page_result';
} //end loadResult

function removeAllChild(parentBox) {
    //empty content_box_quiz (tantque a enfant => suppr)
    while (parentBox.hasChildNodes()) {
        parentBox.removeChild(parentBox.firstChild);
    }
}

export { loadResult };
